import React, {useEffect, useRef, useState} from 'react';
import {Select} from "antd";
import debounce from 'lodash/debounce';

const {Option} = Select;

const token = "eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiJjZjYzOTU3YmQ2MTgxZTdhMWY0MDk1ZmY4MDZlNmNjZSIsIm5iZiI6MTczMzI0ODIwMC4wNSwic3ViIjoiNjc0ZjQ0YzgyNmYwN2MzNTU4NjE5YjQ0Iiwic2NvcGVzIjpbImFwaV9yZWFkIl0sInZlcnNpb24iOjF9.a4ncf2s8Mk6jAJhVoWUoq6s39SdHE-BL5AV1IuazkCA"
const headers = new Headers({
    'Content-Type': 'application/json',
    'Authorization': `Bearer ${token}`,
});
const url = "https://api.themoviedb.org/3/search/multi?query="


const MyComponent = () => {
    const [options, setOptions] = useState([]); // Store fetched options
    const [loading, setLoading] = useState(false); // Loading state

    const inputRef = useRef(null);

    useEffect(() => {
        if (inputRef.current) {
            inputRef.current.focus();
        }
    }, []);

    const onSearchHandler = (value) => {
        // console.log(value);
    }

    const onInputHandler = (e) => {
        console.log(e.key);
    }

    const fetchOptions = debounce(async (searchText) => {
        if (!searchText) {
            setOptions([]);
            return;
        }

        setLoading(true);

        try {
            const response = await fetch(url + searchText, {
                headers: headers,
            });
            const data = await response.json();

            const formattedOptions = data.results.filter(v => v['media_type'] === 'movie').map((item) => ({
                value: item.id,
                label: `${item.title} - ${item.release_date.slice(0, 4)}`
            }));

            setOptions(formattedOptions);
        } catch (error) {
            console.error('Error fetching options:', error);
        } finally {
            setLoading(false);
        }
    }, 300);

    const onSelectHandler = (value, option) => {

        window.open(`https://vidsrc.xyz/embed/movie/${value}?ds_lang=he`, '_blank')
    }

    return (
        <div style={{width: '100%'}}>
            <Select ref={inputRef}
                    showSearch
                    size="large"
                    placeholder="Type a movie title"
                    allowClear
                    style={{width: '80%'}}
                    suffixIcon={null}
                    onInputKeyDown={onInputHandler}
                    onSearch={fetchOptions}
                    loading={loading}
                    filterOption={false}
                    onSelect={onSelectHandler}>
                {options.map((option) => (
                    <Option key={option.value} value={option.value}>
                        {option.label}
                    </Option>
                ))}
            </Select>
        </div>
    );
};

export default MyComponent;
